.contact{
    margin-left: 128px;
    margin-bottom: 60px;
    padding-top: 50px;
}
.card0{
    box-shadow: 0px 4px 8px 0px #757575;
    border-radius: 0px;

}

.card2{
    margin:  0px 40px ;

}
.card .image{
    padding: 30px;
    height: 100%;
    width: 100%;

}
.border-line{
    border-right: 1px solid #e0e0e0;
}

.line{
    height: 1px;
    width: 45%;
    background-color: #e0e0e0;
    margin-top: 10px;
}

.or{
    display:  flex;
    width: 10%;
    font-weight: bold;
}

::placeholder{
    color: #bdbdbd;
    opacity: 1;
    font-weight: 300;
}

input,textarea{
    padding: 10px 12px;
    border: 1px solid lightgrey;
    border-radius: 10px;
    margin-bottom: 5px;
    margin-top: 2px ;
    width: 100%;
    color: #138781;
    font-size: 1rem;
    letter-spacing: 1px;
    }

    input:focus ,textarea:focus{
        box-shadow: none !important;
        outline: 1px solid #138781 !important;
        outline-width: 0px;

    }
    .button:focus{
        box-shadow: none;
        outline-style: 0px;
        }

    .button{
            background-color: #2c2a1e;
            color: white;
            border-radius: 20px;
            margin-top: 15px;
            padding: 10px;
        }

    .button:hover{
        background-color: #000;
        color: white;
    }
  

    #dark .card0{
        background: #5f264a;
        color: black;
        box-shadow: none;
        background-color: rgba(0, 0, 0, 0.15);

    }

    /* ================= */
    @media (max-width:500px){
        .contact{
            margin: 0;
            padding: 0;
            margin-top: 50px;
            
        }
        .contact .card2{
            margin: 0;
        
        }
        .contact .card2 h6{
            display: flex;
            align-items: center;
            justify-content: center;
        }
    }
    