#project {
  margin: 50px;
  margin-left: 100px;
}
#project hr {
  color: #138781;
  width: 400px;
  margin: 10px auto;
}
#project h2 {
  font-weight: bold;
  color: #138781;
}

.card-notify-badge {
  position: absolute;
  left: -10px;
  top: -20px;
  background: #f29f67;
  text-align: center;
  border-radius: 30px;
  color: black;
  padding: 5px 10px;
  font-size: 14px;
}

.card img {
  height: 350px;
  width: 100%;
}

.card-detail-badge {
  background-color: #e0b50f;
  text-align: center;
  border-radius: 30px;
  color: black;
  padding: 5px 10px;
  font-size: 14px;
  display: inline-block;
  margin-left: 5px;
}

.card:hover {
  background-color: white;
  border-radius: 4px;
  box-shadow: 12px 15px 20px rgba(46, 61, 73, 0.15);
  transition: all 1s ease;
}

.ad-btn {
  text-transform: uppercase;
  width: 150px;
  height: 40px;
  display: block;
  text-align: center;
  background-color: #138781;
  color: white;
  border-radius: 80px;
  font-size: 16px;
  line-height: 35px;
  text-decoration: none;
  margin: 20px auto;
  overflow: hidden;
  position: relative;
}
.ad-btn:hover {
  background-color: transparent;
  color: #138781;
  border: 2px solid #138781;
  transition: all 0.5s ease;
  box-shadow: 12px 15px 20px rgba(46, 61, 73, 0.15);
}

@media (max-width: 500px) {
  #project {
    margin: 0;
    padding: 0;
    margin-top: 50px;
  }
  #project hr {
    width: 280px;
  }
  #project .card {
    margin-top: 30px;
  }
}

#dark .card-body {
  color: black;
}
.card-container {
  max-width: 100%;
  height: 100%;
  margin: 0 auto;
  padding: 10px;

}

